* {
  background-color: #ECF0F1;
}
.head {

  padding: 25px;
  width: 300px;
  margin: auto;
}

.close:hover{
  color: red;
}

.wells {
  margin: auto;
  margin-bottom: 10px;
  align-items: center;
  font-size: medium;
  color: #000000;
  border-radius: 10px;
  background-color: #D3D3D3;
}

input[type=checkbox] {
  margin-right: 10px;
  font-size: 30px;
}

.tabs {
  margin: 10px;
  width: auto;
}

.content {
  max-width: 500px;
}

span {
  padding: 5px;
  position: auto;
  background-color: transparent;
}

#checkBox {
  float: left;
  width: 10%;
  background-color: transparent;
  padding: 10px;
}

#todoItem {
  float: left;
  width: 80%;
  background-color: transparent;
  padding: 10px;
}

#closeButton {
  float: right;
  width: 10%;
  background-color: transparent;
  padding: 10px;
}